//index.js
//获取应用实例
// const app = getApp()
// const api = require('../../common/request.js')
import api from '../../common/request.js'

export default {
	data() {
		return {
			motto: 'Hello World',
			userInfo: {},
			hasUserInfo: false,
			// canIUse: wx.canIUse('button.open-type.getUserInfo'),
			bannerlist: [],
			list1: [],
			list2: [],
			list3: [],
			list4: [],
			list5: [],
			list6: [],
			list7: [],
			list8: []
		}
	},
	onLoad() {
		this.getHomeList()
	},
	methods: {
		// 字数转换
		wordsCount(num) {
			if (num > 10000) {
			  return Math.round(num / 10000) + '万字'
			} else {
			  return num + '字'
			}
		},
		goBookDetails(e) {
			console.log(1);
		  let bookId = e.currentTarget.dataset.item.url
		  uni.navigateTo({
				url: `../book/book?id=${bookId}`
		  })
		},
		getUserInfo: function(e) {
		  console.log(e)
		  app.globalData.userInfo = e.detail.userInfo
		  this.setData({
			userInfo: e.detail.userInfo,
			hasUserInfo: true
		  })
		},
		// 下拉刷新
		onPullDownRefresh() {
		  // 导航条上的加载动画
		  uni.showNavigationBarLoading()
		  setTimeout(() => {
			uni.hideNavigationBarLoading()
			// 停止当前页面的下拉刷新
			uni.stopPullDownRefresh()
		  }, 1000)
		},
		// 获取hotList
		getHomeList() {
		  api.get('/index/miniBanner').then(res => {
				this.bannerlist = res.data
		  })
		  Promise.all([
			api.get('/index/miniChart', {sort: 1}),
			api.get(`/index/miniChart`, {sort: 2}),
			api.get(`/index/miniChart`, {sort: 3}),
			api.get(`/index/miniChart`, {sort: 4}),
			api.get(`/index/miniChart`, {sort: 5}),
			api.get(`/index/miniChart`, {sort: 6}),
			api.get(`/index/miniChart`, {sort: 7}),
			api.get(`/index/miniChart`, {sort: 8})
		  ]).then(res => {
			  this.list1 = res[0].data
			  this.list2 = res[1].data
			  this.list3 = res[2].data
			  this.list4 = res[3].data
			  this.list5 = res[4].data
			  this.list6 = res[5].data
			  this.list7 = res[6].data
			  this.list8 = res[7].data
		  }).catch(e => {
				console.log(e)
		  })
		 
		 // uni.request({
   //      url: 'https://mini.haokanread.com/index/banner'
   //  }).then(data => {
			// //data为一个数组，数组第一项为错误信息，第二项为返回数据
			// var [error, res]  = data
			// console.log(res.data)
			// this.bannerlist = res.data.data
			// console.log(this.bannerlist)
   //  })
		}
		
	}
}
